<template>
	<div id="user-agreement">
		<van-popup v-model="popupStatus">
			<div class="popup-title van-hairline--bottom">
				<span class="font-bold">用户服务协议和隐私政策概要</span>
				<i class="icon icon-close" @click="onTogglePopupStatus" v-if="0"></i>
			</div>
			<div class="popup-content" v-html="agreement"></div>
			<ul class="popup-footer flex-lr font-15 van-hairline--top">
				<li class="c-gray van-hairline--right" @click="onCancel">暂不使用</li>
				<li class="c-danger" @click="onConfirm">同意</li>
			</ul>
		</van-popup>
	</div>
</template>

<script>
	export default {
		props: {
			agreement: '',
		},
		data() {
			return {
				popupStatus: true
			}
		},
		methods: {
			onTogglePopupStatus() {
				var that = this;
				that.popupStatus = !that.popupStatus;
			},
			onCancel() {
				var that = this;
				that.util.closeApp();
				that.onTogglePopupStatus();
			},
			onConfirm() {
				var that = this;
				that.util.setStorage('userAgreement', 1)
				that.onTogglePopupStatus();
			}
		},
	}
</script>

<style>
	#user-agreement .van-popup{
		width: 80%;
		min-height: 60%;
		max-height: 80%;
		border-radius: 5px;
		z-index: 2002!important;
	}
	#user-agreement .van-overlay{
		z-index: 2001!important;
	}
	#user-agreement .van-popup .popup-title{
		position: relative;
		height: 44px;
		line-height: 44px;
		font-size: 15px;
		text-align: center;
	}
	#user-agreement .van-popup .popup-title .icon-close{
		position: absolute;
		right: 5px;
		top: 5px;
		line-height: 1;
		color: #999;
		font-size: 16px;
	}
	#user-agreement .van-popup .popup-content{
		position: absolute;
		top: 44px;
		bottom: 44px;
		left: 0;
		right: 0;
		line-height: 1.5;
		padding: 15px;
		overflow-y: scroll;
		overflow-x: hidden;
		-webkit-overflow-scrolling: touch;
		overflow-scrolling:touch;
		font-size: 14px;
	}
	#user-agreement .van-popup .popup-content::-webkit-scrollbar{
		display: none;
	}
	#user-agreement .van-popup .popup-footer{
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 44px;
		text-align: center;
		font-weight: bold;
	}
	#user-agreement .van-popup .popup-footer li{
		height: 100%;
		flex: 1;
		line-height: 44px;
	}
</style>


